import React, { Component } from "react";
import { Button, Icon, Input, AutoComplete } from "antd";
import debounce from "lodash/debounce";

import axios from "axios";

import "./searchfieldcategory.css";
import { Constants } from "../../../utils/index";

const Option = AutoComplete.Option;
const OptGroup = AutoComplete.OptGroup;

class searchFieldCategory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      options: [],
      open: false,
      value: null,
      option:null
    };
    this.query = debounce(this.query, 300);
  }

  handleChange = value => {
    this.setState({
      value
    });
    if (this.props.onChange && value) {
      this.props.onChange(value);
    }
  };
  handleSearch = value => {
    if (value)
      this.query(value, options => this.setState({ options, open: true }));
  };
  handleSelect = (value,option) => {
    this.setState({
      value,
      open: false,
      option
    });
    
    if (this.props.onSelect) {
      this.props.onSelect(value,option);
    }
  };
  handleOpen = () => {
    this.setState({ open: !this.state.open });
    let {value,option} = this.state;

    if(!option){
      var values = value[0].split(',');
      option = {
        props:{
          record:{
            City:values[0],
            State:values[1].replace(/ /g,'')
          }
        }
      }
      this.setState({
        value,
        option
      });
    }
    if (typeof this.props.onChange !== "undefined") {
      if (typeof value === "object") {
        value = value[0];
      }
      this.props.onChange(value,option);
    }
    if (typeof this.props.onOK !== "undefined") {
      if (typeof value === "object") {
        value = value[0];
      }
      this.props.onOK(value,option);
    }
  };
  query = (value, callback) => {
    let { url } = this.props;
    let params = {
      query: value
    };

    if (typeof url === "undefined") {
      url = `${Constants.URL_API_COMPROPERTEE}${Constants.URL_AUTOCOMPLETE}`;
    }
    return axios
      .get(url, { params })
      .then(res => {
        let options = res.data
          .filter(item => item.children.length > 0)
          .map(group => (
            <OptGroup key={group.title} label={group.title}>
              {group.children.map(opt => (
                <Option
                  key={opt.title}
                  value={opt.title}
                  record={{ ...opt }}
                  category={group.title}
                >
                  {opt.title}
                  <span className="certain-search-item-route">
                    {group.title}
                  </span>
                </Option>
              ))}
            </OptGroup>
          ));

        if (typeof callback !== "undefined") {
          callback(options);
        }
      })
      .catch(err => console.log(err));
  };
  componentDidMount() {
    let { defaultValue } = this.props;
    this.setState({
      value: defaultValue
    });
  }
  render() {
    let { placeholder, size, className, style } = this.props;
    let { options, value, open } = this.state;

    return (
      <div
        className="certain-category-search-wrapper"
        style={style || { width: 250 }}
      >
        <AutoComplete
          className={className || "certain-category-search"}
          dropdownClassName="certain-category-search-dropdown"
          dropdownMatchSelectWidth={false}
          dropdownStyle={{ width: 300 }}
          value={value}
          defaultValue={this.props.defaultValue}
          onChange={this.handleChange}
          onSearch={this.handleSearch}
          onSelect={this.handleSelect}
          size={size || "large"}
          style={{ width: "100%" }}
          dataSource={options}
          open={open}
          placeholder={placeholder || "input here"}
          optionLabelProp={this.props.optionLabelProp || "value"}
        >
          <Input
            suffix={
              <Button
                className="search-btn"
                size="large"
                type="primary"
                onClick={this.handleOpen}
              >
                <Icon type="search" />
              </Button>
            }
          />
        </AutoComplete>
      </div>
    );
  }
}
export default searchFieldCategory;
