//ContactForm.js
import React,{Component} from 'react';

class ContactForm extends Component{

	constructor(props){
		super(props);
		this.state = {
			firstname_contact:'',
			lastname_contact:'',
			email_contact:'',
			phone_contact:'',
			message_contact:'',
			Slug:'',
			Matrix_Unique_ID:'',
			Address:'',
			City:'',
			StateOrProvince:'',
			PostalCode:'',
			url_site:''
		}

	}

	onChange = e => {
		let {value, name} = e.target;
		console.log("value y name", value, name);
		
		this.setState({
			[name] : value
		});
	}

	onSendMessage = e =>{

		let payload = this.state;

		if(typeof this.props.onSubmit !== "undefined"){
			this.props.onSubmit(payload);
		}
		
	}

	componentDidMount(){
		let {
			Slug,
			Matrix_Unique_ID,
			Address,
			City,
			StateOrProvince,
			PostalCode,
			url_site
		} = this.props;

		this.setState({
			Slug,
			Matrix_Unique_ID,
			Address,
			City,
			StateOrProvince,
			PostalCode,
			url_site
		});
	}

	render(){
		let {
			firstname_contact,
			lastname_contact,
			email_contact,
			phone_contact,
			message_contact,
			Slug,
			Matrix_Unique_ID,
			Address,
			City,
			StateOrProvince,
			PostalCode,
			url_site
		} = this.state;

		let {message} = this.props;
		
		
		message_contact = "I am interested in your listing " + Address;

		return(
			<form id="frm_contact_agent" name="form1">
                <div className="col-md-6 form-group">
                    <input type="text" name="firstname_contact" id="firstname_contact"
                           className="form-control" placeholder="First name" value={firstname_contact} onChange={this.onChange}/>
                </div>
                <div className="col-md-6 form-group">
                    <input type="text" name="lastname_contact" id="lastname_contact"
                           className="form-control" placeholder="Last name" value={lastname_contact} onChange={this.onChange}/>
                </div>
                <div className="col-md-12 form-group">
                    <input type="text" name="email_contact" id="email_contact"
                           className="form-control" placeholder="Email Address" value={email_contact} onChange={this.onChange}/>
                </div>
                <div className="col-md-12 form-group">
                    <input type="text" name="phone_contact" id="phone_contact"
                           className="form-control" placeholder="Phone number" value={phone_contact} onChange={this.onChange}/>
                </div>
                <div className="col-md-12 form-group">
                    <textarea name="message_contact" id="message_contact"
                              className="form-control" onChange={this.onChange}
                              value={message_contact}/>
                </div>
                <div className="col-md-12 col-sm-6 col-xs-12 tcenter">
                        <span className="ffs-bs">
                            <input type="button" id="send_message"
                                   value="Request Information"
                                   className="btn btn-large btn-primary"
                                   onClick={this.onSendMessage}/>
                            <input type="hidden" name="slug"
                                   value={Slug}/>
                            <input type="hidden" name="idhome"
                                   value={Matrix_Unique_ID}/>
                            <input type="hidden" name="address"
                                   value={Address}/>
                            <input type="hidden" name="city"
                                   value={City}/>
                            <input type="hidden" name="state"
                                   value={StateOrProvince}/>
                            <input type="hidden" name="zip"
                                   value={PostalCode}/>
                            <input type="hidden" name="url_site"
                                   value={url_site}/>
                            <input type="hidden" name="do" value="contact_me_indiv"/>
                        </span>
                    <div className="msg-response-contact">{message || ''}</div>
                </div>
			</form>
		);
	}
}
export default ContactForm;
