import React, { Component } from 'react';
import "./PropertySearch.css";
import debounce from 'lodash/debounce';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import actions from '../../redux/actions/action_search';
import DrawingView from "../../components/DrawableMap/DrawingView";
import Listing from "../../components/Listing/Listing";
import ListingList from "../../components/Listing/ListingList";
import {api_search} from '../../services';
import {
   Affix
} from 'antd';
import {InfiniteList} from '../../components/com';

class PropertySearch extends Component{
    state = {
        TotalRecords : '',
        TotalPages : '',
        CurrentPage : '',
        Listings : [],
        data:[
        ],
        pagination:{
          CurrentPage:1,
          FetchedRecords:0,
          TotalPages:0,
          TotalRecords:0
        },
        gridView: false,
        listView: true,
        mapView: false,
        loading:false,
        preloading:true,
        hasMore: true,
        price: { min: 0, max: 90000 },
        page: 1,
        top:0
    }

    constructor(props)  {
        super(props);
        this.fetchData = debounce(this.fetchData,300);
        
        this.scrollListRef = React.createRef();
    }

    displaySkeleton = () => {
      let count = 6;
      return Array.apply(null, {length: count}).map(Number.call, Number)
    }
    componentDidMount() {
      this.fetchData((res) => {
        this.setState({
          data: res.data,
          pagination:res.meta,
          preloading:false,
          loading:false
        });
      });
      
    }
    fetchData = (callback) => {

      let  { params } = this.props.match;
      let {city,state} = params;
      let data = this.props.search.data || {};
      
      data = {
        ...data,
        Address: `${city},${state}`,
        City:city,
        StateOrProvince:state,
        limit:30
      }
       
      
      
      this.setState({
        loading:true,
        preloading:true
      })

       /* Search API */
       api_search({params:data})
       .then((res) => {
        var response = res.data;
        /*  this.state.page +=1; */
        callback(response);
       })
       .catch((err)=>{
           console.error(err);
       });
    }
    handleFormValue = (value) => {
      this.state.page = 1;
      this.setState({ price: value, hasMore: true });
      this.fetchData((res) => {
        this.setState({
          data: res.data,
          pagination:res.meta
        });
      });
    }
    onChange = (page) => {
      const infiniteList = this.scrollListRef.current;    
      let data = infiniteList.displaySkeleton();

      let search = this.props.search.data || {};

      this.props.actions.changeSearch({
          ...search,
          page
      });
      this.setState({
        page,data
      });
      this.fetchData((res) => {
        this.setState({
          data: res.data,
          pagination:res.meta,
          preloading:false
        });
      });

    }
    componentWillReceiveProps(nextProps, nextContext) {
      
      this.fetchData((res) => {
        
        this.setState({
          data: res.data,
          pagination:res.meta,
          preloading:false,
          loading:false
        });
      });
    }
    render() {
        const { Listings,view, mapView, gridView, listView, price, handleFormValue } = this.props;

        let {loading,preloading,data,pagination} =  this.state;

        return(
          <div className="tabs" style={{ width: '100%',height: '100%' }}>
            <div className="tab-content" style={{ width: '100%',height: '100%' }} >
            {
              (mapView || gridView || listView) &&
              <div id="tab1" className="tab" style={{ 'display': 'block',height: '100%' }}>
                {
                mapView &&
                
                  <Affix className="sidebar col-sm-5 col-xs-12 sidemap" style={{ 'display': 'block',height: '100%' }}>
                      <div id="map">
                          {
                            data.length > 0 && !preloading &&  <DrawingView key={data.length} Listings={data}/>
                          }
                      </div> 
                  </Affix>
                }
                <div className={(mapView)?"content col-sm-7 col-xs-12 view-container":"col-xs-12 content_2"} >
                    <div className={"col-md-12 clist"} style={{marginLeft:0, marginRight:0}}>
                    <div className="wide-2" style={{height:'100vh',marginTop:'10px'}}>
                      <div className="col-xs-12 listing-container" >
                        <div className="row" id="prop-map" >
                          <InfiniteList 
                            ref={this.scrollListRef}
                            className={view}
                            loadMore={this.handleInfiniteOnLoad}
                            onChange={this.onChange}
                            loading={loading}
                            preloading={preloading}
                            data={data}
                            pagination={pagination}
                            hasMore={this.state.hasMore}
                            renderItem={(item) => ((listView)?<ListingList listingData={item}/>:<Listing listingData={item}/>)}
                            />
                          </div>
                        </div>
                      </div>
                </div>
                </div>
              </div>
            }
          </div>
        </div>
      )
    }
}

const mapStateToProps = (state) => {
  const { searchResult, search } = state;
    return {
      search: search,
      result: searchResult || null
    }
}

const mapDispatchToProps = (dispatch) => ({
    actions: bindActionCreators(actions,dispatch)
});



export default connect(mapStateToProps,mapDispatchToProps)(PropertySearch);