import React, {Component} from 'react';
import { Modal } from 'antd';

class MyModal extends Component{
    onOk = visible => {
        if(this.props.onOk){
            this.props.onOk(visible);
        }
    }
    onCancel = visible => {
        if(this.props.onCancel){
            this.props.onCancel(visible);
        }
    }
    render(){

        let {children,title,visible,footer,width=800} = this.props;
        return(
            <Modal
            title={title}
            centered
            visible={visible || false}
            maskClosable={true}
            onOk={() => this.onOk(false)}
            onCancel={() => this.onCancel(false)}
            width={width || 800}
            footer={footer || null}
          >
           {children}
          </Modal>
        );
    }
}
export default MyModal;