import React, {Component} from 'react';
import {  Form, Row, Col } from 'antd';
import './form.css'
class AdvancedForm extends Component{
    constructor(props){
        super(props);

        this.state = {
            expand: false,
            formLayout:'vertical'
        };
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(typeof this.props.onSubmit !== "undefined"){
                this.props.onSubmit(err,values);
                if(!err)
                    this.props.form.resetFields();
            }
        });
    }

    handleReset = () => {
        this.props.form.resetFields();
    }
    toggle = () => {
        const { expand } = this.state;
        this.setState({ expand: !expand });
    }
    handleChange = (key,value) => {
        this.props.form.setFieldsValue({
            [key]: value,
        });
    }
    // To generate mock Form.Item
    getFields() {
        const { getFieldDecorator } = this.props.form;
        const {children} = this.props;
        let childrens = [];

        let { formLayout } = this.state;
        formLayout = this.props.formLayout || formLayout;

        console.log("formLayout:: ",formLayout);
        const formItemLayout = (formLayout === 'horizontal') ? {
            span:12
        } : null;
        
        if(typeof children !== "undefined"){

            try{
                let {props} = children;
                let childNodes = props.children || [];
                childrens = React.Children.map(childNodes, (child,index) => {
                    if(!child) return child;
                    let {name,label,required,message,validator,style,initial} = child.props;
                    
                    style = style || {};

                    delete child.value;

                    return <Col {...formItemLayout} key={index} style={{...style,padding:5,display: "inline-block"}} className={(typeof child.className !== "undefined")?"item-form "+child.className:"item-form"}>
                        <Form.Item label={label}  >
                        {getFieldDecorator((name || `field-${index}`), {
                            initialValue:initial,
                            rules: validator || [{
                                required: (required || false),
                                message: (message || 'Input something!')
                            }],
                           
                            onChange:this.handleChange
                            })(
                                React.cloneElement(child, {
                                    style:{width:'100%'}                                    
                                })
                        )}
                        </Form.Item>
                    </Col>
                })
            }catch(err){
                console.log("ERROR: ",err);
            }

        }
        /* for (let i = 0; i < 10; i++) {
        childrens.push(
            <Col span={8} key={i} style={{ display: i < count ? 'block' : 'none' }}>
                <Form.Item label={`Field ${i}`}>
                    {getFieldDecorator(`field-${i}`, {
                    rules: [{
                        required: true,
                        message: 'Input something!',
                    }],
                    })(
                    <Input placeholder="placeholder" />
                    )}
                </Form.Item>
            </Col>
        );
        } */
        return childrens;
    }

    componentDidMount(){
        let {formLayout} = this.props;
        if(typeof formLayout !== "undefined")
            this.setState({
                formLayout
            });
    }
    render(){
        const { formLayout } = this.state;
        let {footer,className,style} = this.props;
        return(
            <Form
                className={className || "ant-advanced-search-form"}
                layout={formLayout}
                onSubmit={this.handleSubmit}
                style={style||{margin:20}}
            >
                <Row gutter={24}>{this.getFields()}</Row>
                <Row className={(typeof footer !== "undefined")?"footer-advanced-form":null}>
                    {footer}
                </Row>
                {/* <Row>
                    <Col span={24} style={{ textAlign: 'right' }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                        <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
                        Clear
                        </Button>
                        <a style={{ marginLeft: 8, fontSize: 12 }} onClick={this.toggle}>
                        Collapse <Icon type={this.state.expand ? 'up' : 'down'} />
                        </a>
                    </Col>
                </Row> */}
            </Form>
        );
    }
}
const WrappedAdvancedForm = Form.create({ name: 'search_form' })(AdvancedForm);

export default WrappedAdvancedForm;