import React from 'react';
import ReactDOM from 'react-dom';

import Home from './pages/Home/Home';
import PropertySearch from './pages/PropertySearch/PropertySearch';
import PropertySearchData from './pages/PropertySearch/PropertySearchData';
/*import PropertySearch_Grid from './pages/PropertySearch_Grid/PropertySearch_Grid';
import PropertySearch_List from './pages/PropertySearch_List/PropertySearch_List';*/
import PropertyDetails from './pages/PropertyDetails/PropertyDetails';
import PropertyMap from './components/PropertyMap/PropertyMap';
import Pricing from './pages/Pricing/Pricing';
import About from './pages/About/About';
import Contact from './pages/Contact/Contact';
import Subdivision from './pages/Subdivision/Subdivision';
import Sitemap from './pages/Sitemap/Sitemap';

/* import ListingView from './pages/listings/ListingView' */
import App from './App';

import {BrowserRouter as Router,Route, Switch} from 'react-router-dom';
import * as serviceWorker from './serviceWorker';

import rootReducer from './redux/reducers/index';
import {createStore, applyMiddleware, compose} from 'redux';
import {Provider} from 'react-redux';
import thunk from 'redux-thunk';

import "antd/dist/antd.css";
/* import './assets/bootstrap/css/bootstrap.min.css' */

import {Login,Register,PasswordReset} from './pages/Authentication';

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(
  rootReducer,
  composeEnhancers(
		applyMiddleware(thunk)
	)
);

/* store.subscribe(function(){
    console.log(store.getState());
}); */
// First, checks if it isn't implemented yet.
if (!String.prototype.format) {
    String.prototype.format = function() {
      var args = arguments;
      return this.replace(/{(\d+)}/g, function(match, number) { 
        return typeof args[number] != 'undefined'
          ? args[number]
          : match
        ;
      });
    };
  }
const renderRoutes = () => {
    return (
        <Router>
            <Provider store={store}>
                <div>
                    <Switch>
                        <Route exact path="/" component={Home}></Route>
                        
                        <Route exact path="/listings/search/:city/:state/:transaction_type?/:dynamicParams?" 
                        
                        render={ props => 
                        <AppRoute Layout={PropertySearch}
                         Component={PropertySearchData}
                          props={props} /> }/>

                        <Route exact path="/listings/search/:zipcode/:transaction_type?/:dynamicParams?" 
                        
                        render={ props => 
                        <AppRoute Layout={PropertySearch}
                         Component={PropertySearchData}
                          props={props} /> }/>

                        <Route exact path="/listings/search/:adreess/:transaction_type?/:dynamicParams?" 
                        
                        render={ props => 
                        <AppRoute Layout={PropertySearch}
                         Component={PropertySearchData}
                          props={props} /> }/>  
                        

                        <Route exact path="/dashboard/Login" component={Login}></Route>
                        <Route exact path="/dashboard/register" component={Register}></Route>
                        <Route exact path="/dashboard/password-reset" component={PasswordReset}></Route>

                        
                        
                        {/* <Route exact path="/listings/:city/:state" component={ListingView}></Route>   */}
                        <Route exact path="/map" component={PropertyMap}></Route>
                        <Route exact path="/app" component={App}></Route>
                        <Route exact path="/listings/details/:slug/:id" component={PropertyDetails}></Route>
                        <Route exact path="/pricing_plans" component={Pricing}></Route>
                        <Route exact path="/about-us" component={About}></Route>
                        <Route exact path="/contact-us" component={Contact}></Route>
                        <Route exact path="/subdivision/miami-beach/:condo" component={Subdivision}></Route>
                        <Route exact path="/sitemap" component={Sitemap}></Route>
                    </Switch>
                </div>
            </Provider>
        </Router>
    )
}

const AppRoute = ({ Component, Layout, props }) => {
if (Layout) {
    return (
    <Layout {...props}>
        <Component {...props} />
    </Layout>
    );
} else if (!Component) {
    return <Layout {...props} />;
} else {
    return <Component {...props} />;
}
};

ReactDOM.render(renderRoutes(), document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();