import React, { Component } from 'react';
import "./Pricing.css";
import Footer from "../../components/Footer/Footer";


class Pricing extends Component {
    constructor(props) {
        super(props);

        this.state = {
            address: 'Miami,FL',
            type: null
        }
    }

    handleClick = () => {
        const { history } = this.props;
        let { address, type } = this.state;
        let path = `/listings/search/`;
        if(address) {
            address = address.split(',');
            path = `/listings/search/${address[0] || 'Miami'}/${address[1] || 'FL'}`;
        } else {
            path = `/listings/search/${address[0] || 'Miami'}/${address[1] || 'FL'}`;
        }
        path = path + `/${type || 'for_sale'}`;
        history.push(path);
    }
    
    render() {

        return(
            <div className="page-pricing" id="page-top">

	
	<div className="wrapper">
		
		<div id="header" className="menu-wht">
			
		</div>
		
		<div id="page-content">
			
			<div className="container">
				<header className="header hd-cnt">
					<h2>Compropertee Plan Bruno</h2>
					<p>It is easy and takes 15 min maximum</p>
				</header>
				<section id="pricing-3">
					<div className="row">
						<div className="col-md-offset-2 col-md-3 col-sm-4">
							<div className="price-box">
								<span className="header-title">Free</span>
								<div className="price">
									<figure>$0</figure>
									<span className="h6">/ month</span>
								</div>
								<ul>
									<li>Create Blog</li>
									<li>Social Media Accounts</li>
									<li>SEO</li>
									<li>Test Sites Speed / Optimatization</li>
									<li>Sitemap / Indexation Google</li>
									<li>Sign In / Out</li>
								</ul>
								<a href="/#" className="btn btn-block btn-primary">Sign Up</a>
							</div>
						</div>
						<div className="col-md-3 col-sm-4">
							<div className="price-box promoted">
								<div className="popular">
									<p>Most popular</p>
								</div>
								<span className="header-title">Silver</span>
								<div className="price">
									<figure>$20</figure>
									<span className="h6">/ month</span>
								</div>
								<ul>
									<li>Create Blog</li>
									<li>Social Media Accounts</li>
									<li>SEO</li>
									<li>Test Sites Speed / Optimatization</li>
									<li>Sitemap / Indexation Google</li>
									<li>Sign In / Out</li>
								</ul>
								<a href="/#" className="btn btn-block btn-primary">Sign Up</a>
							</div>
						</div>

						<div className="col-md-3 col-sm-4">
							<div className="price-box">
								<span className="header-title">Gold</span>
								<div className="price">
									<figure>$40</figure>
									<span className="h6">/ month</span>
								</div>
								<ul>
									<li>Create Blog</li>
									<li>Social Media Accounts</li>
									<li>SEO</li>
									<li>Test Sites Speed / Optimatization</li>
									<li>Sitemap / Indexation Google</li>
									<li>Sign In / Out</li>
								</ul>
								<a href="/#" className="btn btn-block btn-primary">Sign Up</a>
							</div>
						</div>
					</div>
				</section>
			</div>
		</div>

		
    <div id="footer">
    	<Footer/>
    </div>
	</div>
</div>
            
        );
    }
}

export default Pricing;