import {Constants,ApiGO,Api} from '../utils';
/* API Gooneworld */
/* 
* @api_autocomplete: {search_param}
*/
const api_autocomplete = (params) => {
    params["limit"] = 30;
    return ApiGO.get(Constants.URL_AUTOCOMPLETE+'?cache=120',{params});
}
/* 
* @recent_listings: {city}
*/
const recent_listings = (params) => {
    return ApiGO.get(Constants.URL_RECENT_LISTING+'?cache=120',{params});
}


/* API */
/* 
* @api_search: {params}
*/
const api_search = (params) => {
    params["limit"] = 30;
    return Api.get(Constants.URL_API_SEARCH+'?cache=120',{params});
}
/* 
* @find_lat_lng: {listingId}
*/
const find_lat_lng = (listingId) => {
    return Api.get(Constants.URL_API_FIND_LAT_LNG+listingId,{cache:120});
}

/* 
* @pothos: {_id, index:0}
*/
const pothos = (_id,index) => {
    index = index || 0;
    return ApiGO.get(Constants.URL_POTHOS+_id+'/'+index,{cache:120});
}
/* 
* @search_autocomplete: {params}
*/
const search_autocomplete = (params) => {
    params["limit"] = 30;
    return Api.get(Constants.URL_AUTOCOMPLETE_SEARCH,{params});
}
/* 
* @login: {params}
*/
const login = (params) => {
    return new Promise((resolve,reject) => {
        Api.post(Constants.URL_LOGIN,params)
        .then(response => {
            let {data} = response;
            Api.defaults.headers.common['Authorization'] = data.id;
            resolve(response);
        },reject);
    });
}
/* 
* @logout: {params}
*/
const logout = (params) => {
    Api.defaults.headers.common['Authorization'] = localStorage.access_token;
    return new Promise((resolve,reject) => {
        Api.post(`${Constants.URL_LOGOUT}?access_token=${localStorage.access_token}`)
        .then(response => {
            resolve(response);
        },error => {
            reject(error);
        });
    });
}
/* 
* @signup: {params}
*/
const signup = (params) => {
    return Api.post(Constants.URL_SIGNUP,params);
}
/* 
* @save_search: {params}
*/
const save_search = (params) => {
    Api.defaults.headers.common['Authorization'] = localStorage.access_token;
    return new Promise((resolve,reject) => {
        Api.post(Constants.URL_SAVE_SEARCH,params)
        .then(response => {
            resolve(response);
        },reject);
    });
}

const send_message = (params) => {
    return ApiGO.post(Constants.URL_SEND_MESSAGE+'?cache=120',{params});
}

const calcula_down_payment = (params) => {
    return ApiGO.get(Constants.URL_SEND_MESSAGE+'?cache=120',{params});
}

/* 
* @add_favorite: {params}
*/
const add_favorite = (params) => {
    Api.defaults.headers.common['Authorization'] = localStorage.access_token;
    return new Promise((resolve,reject) => {
        Api.post(Constants.URL_ADD_FAVORITES.format(params.userId,params.Matrix_Unique_ID))
        .then(response => {
            resolve(response);
        },reject);
    });
}
const get_user = userId => {
    Api.defaults.headers.common['Authorization'] = localStorage.access_token;
    return Api.get(`${Constants.URL_USERS}/${userId}`);
}
const update_user = params => {
    Api.defaults.headers.common['Authorization'] = localStorage.access_token;
    let userId = localStorage.userId;
    return Api.put(`${Constants.URL_USERS}/${userId}`,params);
}
const change_password = params => {
    Api.defaults.headers.common['Authorization'] = localStorage.access_token;
    return Api.post(`${Constants.URL_CHANGE_PASSWORD}`,params);
}
const password_reset = params => {
    return Api.post(`${Constants.URL_USERS}/reset`,params);
}

export{
    api_autocomplete,
    recent_listings,
    api_search,
    pothos,
    find_lat_lng,
    search_autocomplete,
    send_message,
    login,
    logout,
    signup,
    save_search,
    calcula_down_payment,
    add_favorite,
    get_user,
    update_user,
    change_password,
    password_reset,
}