import React, {Component} from 'react';
import { withRouter } from 'react-router'
import {Input,Icon, Button,Row,message} from 'antd';
import './passwordreset.css';
import {AdvancedForm} from '../../index';
import {Link} from "react-router-dom";
import {password_reset} from '../../../../services';

class PasswordResetForm extends Component{
    constructor(props){
        super(props);
        this.state = {
            email:null
        }
    }

    onSubmit = (err,params) => {
        let {history} = this.props;
        if(!err){

            password_reset(params)
            .then(response => {
                let {data} = response;
                if(typeof this.props.onSubmit !== "undefined")
                    this.props.onSubmit(data);
                    localStorage.clear();
                    history.push("/dashboard/login");
                    message.info('Please check your email!');
            })
            .catch(err => console.log(err));
        }
    }
    render(){
        return(
            <AdvancedForm formLayout="vertical"  onSubmit={this.onSubmit}  footer={
                <div>
                    <Row>
                        <Button size="large" style={{width:"100%"}} type="primary" htmlType="submit" className="login-form-button">
                            SEND
                        </Button>
                        Do u have an account?  <Link to="/dashboard/login">Sign In</Link> Or <Link to="/dashboard/register">register now!</Link>
                    </Row>
                </div>
            }>
                <div> 
                    <p style={{textAlign:"center"}}>Please input your email to send a password.</p>
                    <Input size="large"  style={{width:'100%'}} prefix={<Icon type="email" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Email" type="email" name="email" validator={[{ required: true, message: 'Please input your email!' }]}/>
                </div>
            </AdvancedForm>
        );
    }
}
export default withRouter(PasswordResetForm);