import React, { Component } from 'react';


class Field extends Component {
    state = {
        namefield : '',
        contenido : '',
        columnas : ''
    }


    constructor(props){
        super(props);
        //console.log(props);

        /*this.setState({
          namefield:this.props.namefield,
          contenido:this.props.contenido,
          columnas:2
        });*/
        this.state.namefield = this.props.namefield;
        this.state.contenido = this.props.contenido;
        this.state.columnas = 6;
    }

    render() {
        var none = " dnone";
        if(this.state.contenido === "0" || this.state.contenido === "No Data" || this.state.contenido === "No D" || this.state.contenido === "No Da"  || this.state.contenido === "No Dat" || this.state.contenido === ""){
            none = "dnone";
        }else{
            none = "qv-list-wrapper";
        }
        return (
            <div className={none}>
                <li className='col-md-6 col-xs-12'><div style={{'display':'table-cell','paddingRight':4+'px'}}>{this.state.namefield}:</div><div style={{'display':'table-cell'}}>{this.state.contenido}</div></li>
            </div>
        );
    }

    componentDidUpdate(prevProps, prevState){
        if (prevProps.contenido !== this.props.contenido) {
            this.setState({
                namefield:this.props.namefield,
                contenido:this.props.contenido,
                columnas:this.props.columnas
            });
        }
        //console.log(this.state);
    }

}

export default Field;