import React, {Component} from "react";
import {GoogleMap, withGoogleMap, Marker,InfoWindow} from "react-google-maps";
const {compose, withStateHandlers} = require("recompose");
/*
 * Sample From: https://developers.google.com/maps/documentation/javascript/examples/map-simple
 */
const SimpleMapExampleGoogleMap = compose(
    withStateHandlers(() => ({
        isOpen: true,
    }), {
        onToggleOpen: ({isOpen}) => () => ({
            isOpen: !isOpen,
        })
    }),
    withGoogleMap
)(props => (
    <GoogleMap
        defaultZoom={18}
        defaultCenter={{lat:props.geoCode.latitude,lng:props.geoCode.longitude}}
    >
        <Marker
            position={{lat:props.geoCode.latitude,lng:props.geoCode.longitude}}
            onClick={props.onToggleOpen}
        >
            {props.isOpen && <InfoWindow onCloseClick={props.onToggleOpen}>
                <span>{props.geoCode.formattedAddress}</span>
            </InfoWindow>}
        </Marker>
    </GoogleMap>
));

/*
 * Add <script src="https://maps.googleapis.com/maps/api/js"></script> to your HTML to provide google.maps reference
 */
export default class SimpleMap extends Component {

    render() {
        let styleName = this.props.styleName;
        if (!styleName) {
            styleName = 'embed-responsive-21by9'
        }
        return (
            <div>
                <SimpleMapExampleGoogleMap
                    loadingElement={<div style={{height: `100%`}}/>}
                    containerElement={<div style={{height: `350px`}}/>}
                    mapElement={<div style={{height: `100%`}}/>}
                    geoCode={this.props.geoCode}
                />
            </div>
        );
    }

}
