    componentDidMount() {
        this.requestListing();
    }

    componentDidUpdate() {
        this.calculate_down();
        this.mls_historical();
        this.rets_public();
        this.get_description_to_twitter();

        var $preloader = $('#page-preloader');
        $preloader.fadeOut('slow');
        var $spinner = $preloader.find('.gps_ring');
        var $spinner2 = $preloader.find('.gps_ring2');
        $spinner.fadeOut();
        $spinner2.fadeOut();
        //this.calculate_mortgage();
    }

    formatNumber(num) {

        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
    }


    requestListing() {

        let data = {
            slug: this.state.Slug
        };
        axios.get(PATH_API_DETAILS, {params: data})
            .then((res) => {
                if (res.status === 200) {
                    
                    let images = res.data.images;
                    let item = res.data.data[0];

                    images = images.map((image,index) => {
                        let url = `${Constants.URL_API_PHOTOS}${item.Matrix_Unique_ID}/${index}.jpg`;
                        return url;
                    });

                    this.setState({
                        data: res.data.data[0],
                        similar: res.data.similar,
                        images
                    });
                }
            })
            .catch((err) => {
                console.error(err);
            });
    }

    in_array(needle, haystack) {

        for (var i in haystack) {
            if (haystack[i] == needle) return true;
        }
        return false;
    }

    calculate_mortgage() {

        var formElement = document.getElementById("frm_mortgage");
        var data = this.FormDataToJSON(formElement);
        data["do"] = "calcula_mort";

        axios.get('https://gooneworld.com/func/calculate_mortgage.php', {params: data})
            .then((res) => {
                $(".amount_monthly").html(res.data);
            })
            .catch((err) => {
                console.error(err);
            });
    }

    calculate_down() {
        var formElement = document.getElementById("frm_mortgage");
        var data = this.FormDataToJSON(formElement);
        data["do"] = "calcula_down";

        axios.get('https://gooneworld.com/func/calculate_mortgage.php', {params: data})
            .then((res) => {
                $("#down_payment").val(res.data);
            })
            .catch((err) => {
                console.error(err);
            });
    }

    rets_public() {
        let data = {
            folio_muni: this.state.data.ParcelNumber
        };

        axios.get('https://gooneworld.com/func/detail_historical_view_public_3.php', {params: data})
            .then((res) => {
                $(".conte_2_histo").html(res.data);

            })
            .catch((err) => {
                console.error(err);
            });
    }

    mls_historical() {
        let data = {
            slug: this.state.Slug
        };

        axios.get('https://gooneworld.com/func/detail_historical_view_3.php', {params: data})
            .then((res) => {
                $(".conte_1_histo").html(res.data);

            })
            .catch((err) => {
                console.error(err);
            });
    }

    SendMessage() {
        $.ajax({
            type: "POST",
            url: "https://gooneworld.com/func/send_message.php",
            data: $("#frm_contact_agent").serialize(),
            dataType: "json",
            success: function (datos) {
                $(".msg-response-contact").html(datos.render);
            }
        });
    }


    get_description_to_twitter() {
        let data = {
            slug: this.state.Slug,
            city: this.state.data.City,
            state: this.state.data.StateOrProvince,
            sysid: this.state.data.Matrix_Unique_ID,
            price: this.state.data.ListPrice
        };

        var link_canonical = "http://104.248.211.159:9000/listings/details/" + this.state.data.Slug + "/" + this.state.data.Matrix_Unique_ID;

        axios.get('https://gooneworld.com/func/for_share_social.php', {params: data})
            .then((res) => {
                $(".twitter").attr("href", "https://twitter.com/share?url=" + link_canonical + "&text=" + this.urlEncode(res.data));
                $(".linkedin").attr("href", "http://www.linkedin.com/shareArticle?mini=true&amp;url=" + link_canonical + "&title=" + this.urlEncode(res.data));
            })
            .catch((err) => {
                console.error(err);
            });

        $(".facebook").attr("href", "https://www.facebook.com/sharer/sharer.php?u=" + link_canonical);
        $(".google").attr("href", "https://plus.google.com/share?url=" + link_canonical);

    }


    handleCalculaMort() {
        this.calculate_mortgage();
    }

    handleCalculaDown() {
        this.calculate_down();
    }

    handleChange(e) {
        this.setState({value: e.target.value});
    }

    handleHomePrice(e) {
        this.setState({valuehomeprice: e.target.value});
    }

    handleDown(e) {
        this.setState({valuedown: e.target.value});
    }

    handlePercentage(e) {
        this.setState({valuepercentage: e.target.value});
    }

    handleYears(e) {
        this.setState({valueyears: e.target.value});
    }

    handleRate(e) {
        this.setState({valuerate: e.target.value});
    }

    ShowMap() {

        /*$("#tab1").removeClass("active");
        $("#tab2").addClass("active");
        $("#tab3").removeClass("active");*/
        this.setState({
            simpleMap: <SimpleMap
                geoCode={this.state.data.Geocode}/>,
            tabs: {
                photos: 'tab',
                map: 'tab active',
                streetview: 'tab'
            },
            tabsli: {
                photos: 'col-xs-4',
                map: 'col-xs-4 active',
                streetview: 'col-xs-4'
            }
        });
    }

    ShowPhotos() {
        this.setState({
            tabs: {
                photos: 'tab active',
                map: 'tab',
                streetview: 'tab'
            },
            tabsli: {
                photos: 'col-xs-4 active',
                map: 'col-xs-4',
                streetview: 'col-xs-4'
            }
        });



        /*$("#tab1").addClass("active");
        $("#tab2").removeClass("active");
        $("#tab3").removeClass("active");*/
    }

    ShowStreet() {
        /*$("#tab1").removeClass("active");
        $("#tab2").removeClass("active");
        $("#tab3").addClass("active");*/
        this.setState({
            streetView: <StreetViewPanoramaEx
                coords={{lat: this.state.data.Geocode.latitude, lng: this.state.data.Geocode.longitude}}/>,
            tabs: {
                photos: 'tab',
                map: 'tab',
                streetview: 'tab active'
            },
            tabsli: {
                photos: 'col-xs-4',
                map: 'col-xs-4',
                streetview: 'col-xs-4 active'
            }
        });


    }

    FormDataToJSON(FormElement) {

        var formData = new FormData(FormElement);
        var ConvertedJSON = {};
        for (const [key, value]  of formData.entries()) {
            ConvertedJSON[key] = value;
        }

        return ConvertedJSON
    }

    hasClass(elem, className) {
        return new RegExp(' ' + className + ' ').test(' ' + elem.className + ' ');
    }

    urlEncode(s) {
        return encodeURIComponent(s).replace(/\%20/g, '+').replace(/!/g, '%21').replace(/'/g, '%27').replace(/\(/g, '%28').replace(/\)/g, '%29').replace(/\*/g, '%2A').replace(/\~/g, '%7E');
    }

