import React,{Component} from 'react';
import {LoginForm} from '../../../components/com';

import { message} from 'antd';
import {login} from '../../../services';
import './login.css';


class Login extends Component{
    onSubmit = (err,params) => {
        let {email} = params;
        let {history} = this.props;
        if(err){
            return message.error(err.message);
        }
        login(params)
        .then(response => {
            let {data} = response;
            let username = email.substr(0,email.indexOf('@'));
            console.log("Response: ",data);
            localStorage.setItem("userId",data.userId);
            localStorage.setItem("access_token",data.id);
            localStorage.setItem("username",username);
            
            console.log(history.location);
            history.push('/');

            
        })
        .catch(err => message.error(err.message));
    }
    componentDidMount(){
        let {history} = this.props;
        if(typeof localStorage.userId !== "undefined")
            history.push("/");
    }
    componentWillReceiveProps(e,next){
        console.log("--> ",e,next);
    }
    render(){
        return(
            <section id="wrapper" className="login-register">
                <div className="login-box login-sidebar">
                    <div className="white-box">
                        <div className="conteim">
                        <a href="/" class="db titlogo">CP</a>
                        </div>
                        <LoginForm formLayout="vertical" onSubmit={this.onSubmit}/>
                    </div>
                </div>

                {/*<Layout className="login-page">
                <Header>Sign in to your account</Header>
                <Content>
                    <Row className="login-container">
                        <Col span={12}></Col>
                        <Col span={12} className="conte_form_login">
                        </Col>
                        <Col span={12}></Col>
                    </Row>    
                </Content>
                <Footer>Compropertee 2019</Footer>
            </Layout>*/}
            </section>

            
        );
    }
}
export default Login;