import React, { Component } from 'react';
import axios from 'axios';
import "./Listing.css";
import ComingSoonLogo from '../../assets/img/coming_soon.png';

import {Constants} from '../../utils'
import {SkeletonTpl} from '../../components/com';

import {Icon,message} from 'antd';
import {add_favorite} from '../../services'

class ListingList extends Component{
    state = {
        data : [],
        photo: {
            backgroundImage : `url(https://mediaimpactfunders.org/wp-content/uploads/2017/12/loading-sign-slider.png)`,
            backgroundSize : 'cover',
            backgroundPosition: 'center center'
        }
    }

    formatNumber (num) {
        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")
    }

    getImage() {
        let photo = {
            backgroundImage : `url(http://www.esmeth.com/wp-content/uploads/2014/11/coming_soon_thumb.png)`,
            backgroundSize : 'cover',
            backgroundPosition: 'center center'
        }
        let API_URL = `${Constants.URL_API_PHOTOS}${this.state.data.Matrix_Unique_ID}/0.jpg`;
        axios.get(API_URL)
        .then((res) => {
            if(res.status === 200) {
                photo.backgroundImage = `url(${res.config.url})`;
                this.setState({
                    photo,
                });
            }
        })
        .catch((err) => {
            let API_URL = `${Constants.URL_API_PHOTOS}${this.state.data.Matrix_Unique_ID}/1.jpg`;
            axios.get(API_URL).then((res) => {
                photo.backgroundImage = `url(${res.config.url})`;
                this.setState({ 
                    photo,
                });
            }).catch((err) => {
            })
        });
    }

    /* Custom Functions */
    addFavorite = () => {
        
        if(!localStorage.userId) return 
        let {Matrix_Unique_ID} = this.state.data;
        let paramas = {
            userId:localStorage.userId,
            Matrix_Unique_ID
        }    

        add_favorite(paramas)
        .then(response => {
            let {data} = response;
            message.info(data.description);
        })
        .catch(error => message.error(error.message));
    }
    render() {

        this.state.data = this.props.listingData;
        let updatedPhoto = {};
        if(this.state.data.PhotoCount > 0) {
            updatedPhoto = {
                ...this.state.photo,
                backgroundImage: `url(${Constants.URL_API_PHOTOS}${this.state.data.Matrix_Unique_ID}/0.jpg)`
            }
        } else {
            updatedPhoto = {
                ...this.state.photo,
                backgroundImage: `url(${ComingSoonLogo})`
            }
        }
        this.state.photo = updatedPhoto;
        let { photo } = this.state;
        var transtype="sale";
        var address = "";

        if (this.state.data.StreetNumber !== "No Data") {
            address += " "+this.state.data.StreetNumber;
        }
        if (this.state.data.StreetDirPrefix !== "No Data") {
            address += " "+this.state.data.StreetDirPrefix;
        }
        if (this.state.data.StreetName !== "No Data") {
            address += " "+this.state.data.StreetName;
        }
        if (this.state.data.UnitNumber !== "No Data") {
            address += " # "+this.state.data.UnitNumber;
        }
        
        var address_normal = address;

        var address_show = address_normal.split(",")[0].toUpperCase();
        var lenadd = address_show.length;
        var address_listing = address_show.substr(0,24);
        var puntos = (lenadd>24)?'..':'';

        if(this.state.data.PhotoCount>0 && this.state.data.PhotoCount==1){
            var textphoto='Photo';
        }else if(this.state.data.PhotoCount > 1){
            var textphoto='Photos';
        }else{
            var textphoto='';
        }

        var typeprop = '';

        /*if (this.state.data.StyleofProperty !== "" && this.state.data.StyleofProperty !== "No Data") {
            typeprop = this.state.data.StyleofProperty;
        }else if (this.state.data.TypeofProperty !== "" && this.state.data.TypeofProperty !== "No Data"){
            typeprop = this.state.data.TypeofProperty;
        }*/

        var property_type = "";
        if(this.state.data.PropertyType == "Commercial/Business/Agricultural/Industrial Land"){
            property_type ='Commercial/Business';
        }else{
            property_type = this.state.data.PropertyType;
        }

        var styleproperty = "";
        if (this.state.data.StyleofProperty != "" && this.state.data.StyleofProperty != "No Data") {
            styleproperty = this.state.data.StyleofProperty;
        }else{
            if (this.state.data.TypeofProperty != "" && this.state.data.TypeofProperty != "No Data") {
                styleproperty = this.state.data.TypeofProperty; 
            }else{
                styleproperty = "";
            }
        }

        var areasize = '';

        if(this.state.data.ApproximateLotSize > 0 && this.state.data.ApproximateLotSize !== "" && this.state.data.ApproximateLotSize !== "X" && this.state.data.ApproximateLotSize !== "x" && this.state.data.ApproximateLotSize !== "00" && this.state.data.ApproximateLotSize !== "No Data"){
            areasize = this.state.data.ApproximateLotSize;
            areasize = areasize.replace("Sq.Ft", "");
            areasize = areasize.replace("SqFt", "");
            areasize = areasize.replace("Sqft", "");
            areasize = areasize.replace(".", "");
            areasize = areasize.replace(",", "");
            areasize = this.formatNumber(areasize);
            areasize = areasize+" Sqft";
        }else{
            areasize = "";
        }



        return(
            <span>
            {
                    
               (this.state.data.type !== "dummy")?
                <div className="row white col-lg-12 col-xs-12" style={{'padding':'0px'}}>
                    <div className="col-md-3 col-sm-3 prp-img">
                        <div className="exp-img-2" style={photo}>
                        <span className="filter"></span>
                        <span className="ffs-bs"><label for="op" className="btn btn-small btn-primary">browse photos</label></span>
                        <div className="overlay">
                            <div className="img-counter">{this.state.data.PhotoCount} Photos</div>
                        </div>
                    </div>
                    </div>
                    <div className="item-info col-lg-7 col-md-6 col-sm-6">
                    <h4><a href=""></a></h4>
                    <p className="team-color">{this.state.data.City} ,{this.state.data.StateOrProvince} {this.state.data.PostalCode} </p>
                    <div className="block">
                            <div className="col-md-4 col-sm-6 col-xs-12 cat-img">
                                <p className="info-line">{property_type}</p>
                            </div>
                            <div className="col-md-4 col-sm-6 col-xs-12 cat-img">
                                <p className="info-line">{styleproperty}</p>
                            </div>
                            <div className="col-md-4 col-sm-6 col-xs-12 cat-img">
                                <p className="info-line">{areasize}</p>
                            </div>
                        </div>
                    <div className="col-md-4 col-sm-6 col-xs-12 line"></div>
                    <div className="col-md-4 col-sm-6 col-xs-12 line"></div>
                    <div className="col-md-4 col-sm-6 col-xs-12 line"></div>
                    <hr/>
                    <p></p>
                    </div>
                    <div className="item-price col-lg-2 col-md-3 col-sm-3 col-xs-12">
                    <div className="sum col-sm-12 col-xs-6">
                        <p>${this.formatNumber(this.state.data.ListPrice)}</p>
                        <p className="team-color">For {transtype}</p>
                    </div>
                    <span className="ffs-bs col-xs-12 btn-half-wth"><a href="" className="btn btn-default btn-small">view more <i className="fa fa-caret-right"></i></a></span>
                    <div className="sum favorite col-sm-12 col-xs-6">
                        <div className="bookmark col-xs-3" data-bookmark-state="empty">
                            <Icon type="heart" theme="twoTone" twoToneColor="#ffffff75" style={{fontSize:24, color:"red"}} onClick={this.addFavorite}/>
                            {/* <span className="title-add">Add to bookmark</span> */}
                        </div>
                        <p className="col-xs-3">Fav</p>
                       {/*  <div className="compare col-xs-3" data-compare-state="empty">
                            <span className="plus-add">Add to compare</span>
                        </div> */}
                        <p className="col-xs-3">Comp</p>
                    </div>
                </div>
                </div>:<SkeletonTpl key={this.state.data.id}/>
             }
             </span>
        )
    }
}
export default ListingList;