import React, { Component } from "react";
import "./Home.css";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import MetaTags from "react-meta-tags";
import "react-selectize/themes/index.css";

import "jquery-ui/themes/base/core.css";
import "jquery-ui/themes/base/theme.css";
import "jquery-ui/ui/core";

import { Link } from "react-router-dom";
import { SearchFieldCategory } from "../../components/com";

import { Radio, Row } from "antd";

import { BrowserView} from "react-device-detect";

/**
 * Require Services
 */
import { recent_listings } from "../../services";
import Mobile from "../../layouts/Mobile";
import MHome from './mHome';
class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      address: "Miami,FL",
      type: "for_sale",
      transactionType: "for_sale",
      search: [],
      options: [],
      Listings: []
    };
  }

  onChangeSearch = (value, option) => {
    console.log("Value Selected: ", value);
    this.setState({
      address: value
    });
    if (typeof option !== "undefined") this.goToSearch(value, option);
  };

  handleTransactionType = e => {
    let { value } = e.target;
    this.setState({ type: value });

    /*  let {filters} = this.state;
        filters["transactionType"] = value;
        this.state.filters = {
            ...filters
        }
        this.filter("transactionType"); */
  };
  goToSearch = (value, option) => {
    const { history } = this.props;

    let {
      props: { record }
    } = option;
    let { City, State } = record;

    let { address, type } = this.state;
    let path = `/listings/search/`;
    if (address) {
      if (!State || !City) {
        return console.log("No se ha definido address.");
      }
      State = State.replace(/ /g, "");
      //City = City.replace(/ /g,'-');

      path = `/listings/search/${City || "Miami"}/${State || "FL"}`;
      path = path + `/${type || "for_sale"}`;
      history.push(path);
    }
  };

  componentDidMount() {
    const self = this;
    let params = {
      city: "Miami"
    };
    recent_listings(params)
      .then(res => {
        const { Results } = res.data;
        Results.map(l => {
          self.state.Listings.push(l);
          return l;
        });
      })
      .catch(err => console.log(err));
  }

  render() {
    var meta_title = "Compropertee Commercial Real Estate For Sale and Lease";
    var meta_description =
      "Search commercial real estate for sale and lease. Find your next investment property, industrial space, land or office space. Sell your commercial listings.";

    return (
      <div className="wrapper">
        <BrowserView>
            <MetaTags>
              <title>{meta_title}</title>
              <meta
                name="viewport"
                content="width=device-width, initial-scale=1.0"
              />
              <meta name="description" content={meta_description} />
              <meta property="og:locale" content="en_US" />
              <meta property="og:site_name" content="Compropertee" />
              <meta id="title_og" property="og:title" content={meta_title} />
              <meta property="og:description" content={meta_description} />
              <meta property="og:url" content="https://compropertee.com" />
              <meta property="og:type" content="website" />
              <meta property="og:image" content="" />
              <meta name="twitter:card" content="summary" />
              <meta name="twitter:site" content="@compropertee" />
              <meta name="twitter:title" content={meta_title} />
              <meta name="twitter:url" content="https://compropertee.com" />
              <meta name="twitter:description" content={meta_description} />
              <meta name="twitter:creator" content="@compropertee" />
              <meta name="twitter:image:src" content="" />
              <meta name="twitter:image" content="" />
              <meta name="geo.region" content="US-FL" />
              <meta name="geo.placename" content="Miami" />
              <meta name="geo.position" content="25.789097;-80.204044" />
              <meta name="ICBM" content="25.789097, -80.204044" />
              <meta name="dcterms.source" content="https://compropertee.com" />
              <meta id="title_dc" name="dcterms.title" content={meta_title} />
              <meta name="dcterms.description" content={meta_description} />
            </MetaTags>

            <div id="header" className="menu-wht header_home">
              <Header />
            </div>

            <div id="page-content_home">
              <div className="home_1">
                <div className="header_title">
                  <h1>Find Commercial Real Estate</h1>
                  <h4>
                    Lorem Ipsum is simply dummy text of the printing and
                    typesetting industry.
                  </h4>
                </div>
                <form id="form-submit" className="form-submit">
                  <div className="search">
                    <div className="selector col-md-12  col-sm-12">
                      <Row>
                        <Radio.Group
                          size="large"
                          style={{
                            width: "100%",
                            textAlign: "center",
                            margin: "10px"
                          }}
                          className="home-types"
                          value={this.state.type}
                          onChange={this.handleTransactionType}
                        >
                          <Radio.Button ghost value="for_sale">
                            For Sale
                          </Radio.Button>
                          <Radio.Button ghost value="for_lease">
                            For Lease
                          </Radio.Button>
                        </Radio.Group>
                      </Row>
                    </div>
                    <div id="email-label" className="col-md-12 col-sm-12">
                      <Row>
                        <SearchFieldCategory
                          className="search-home"
                          size="large"
                          style={{ height: "180px" }}
                          onSelect={this.onChangeSearch}
                          onOK={this.onChangeSearch}
                          defaultValue={["Miami, FL"]}
                          placeholder="Where do you like to live?"
                        />
                      </Row>
                    </div>
                  </div>
                </form>
              </div>
              <div className="container">
                <div className="explore2 col-md-12 col-xs-12">
                  <h2>Properties Commercial in Miami</h2>
                </div>
                <div className="row">
                  <div className="col-md-4 col-sm-6">
                    <Link
                      to="/listings/search/Miami/FL/for_sale/propertyType=retail"
                      className="exp-img home_retail"
                    >
                      <span className="filter" />
                      <div className="img-info">
                        <h3>Retail</h3>
                        <span className="ffs-bs btn btn-small btn-primary">
                          explore more
                        </span>
                      </div>
                    </Link>
                  </div>
                  <div className="col-md-4 col-sm-6">
                    <Link
                      to="/listings/search/Miami/FL/for_sale/propertyType=office"
                      className="exp-img home_office"
                    >
                      <span className="filter" />
                      <div className="img-info">
                        <h3>Offices</h3>
                        <span className="ffs-bs btn btn-small btn-primary">
                          explore more
                        </span>
                      </div>
                    </Link>
                  </div>
                  <div className="col-md-4 col-sm-6">
                    <Link
                      to="/listings/search/Miami/FL/for_sale/propertyType=industrial"
                      className="exp-img home_industrial"
                    >
                      <span className="filter" />
                      <div className="img-info">
                        <h3>Industrial</h3>
                        <span className="ffs-bs btn btn-small btn-primary">
                          explore more
                        </span>
                      </div>
                    </Link>
                  </div>
                  <div className="col-md-4 col-sm-6">
                    <Link
                      to="/listings/search/Miami/FL/for_sale/propertyType=multi"
                      className="exp-img home_multi"
                    >
                      <span className="filter" />
                      <div className="img-info">
                        <h3>Multi Family</h3>
                        <span className="ffs-bs btn btn-small btn-primary">
                          explore more
                        </span>
                      </div>
                    </Link>
                  </div>
                  <div className="col-md-8">
                    <Link
                      to="/listings/search/Miami/FL/for_sale/propertyType=shopping"
                      className="exp-img home_buss"
                    >
                      <span className="filter" />
                      <div className="img-info">
                        <h3>Businesses</h3>
                        <span className="ffs-bs btn btn-small btn-primary">
                          explore more
                        </span>
                      </div>
                    </Link>
                  </div>
                </div>
              </div>
              <div className="wide-2">
                <div className="container">
                  <div className="explore">
                    <h2>Our New Exclusive Homes</h2>
                    <h5 className="team-color">
                      Lorem Ipsum is simply dummy text of the printing and
                      typesetting industry. <br />
                      Lorem Ipsum has been the industry standard dummy text ever
                      since
                    </h5>
                  </div>
                  <div className="row">
                    <div className="Listado">{this.displayHomes()}</div>
                  </div>
                </div>
              </div>
            </div>
            <BrowserView>
              <Footer />
            </BrowserView>
        </BrowserView>
        <Mobile>
            <MHome data={this.state.Listings}/>
        </Mobile>
      </div>
    );
  }

  displayHomes() {
    return this.state.Listings.map((l, index) => (
      <div className="col-xs-3 cust-pad" key={index}>
        <div className="pr-img" />
      </div>
    ));
  }
}

export default Home;
