import React, { Component } from 'react';
import "./PropertySearch.css";
import Header_Listing from "../../components/Header_Listing/Header_Listing";
import Search from "../../components/Listing/Search";
import TypeViews from "../../components/Listing/TypeViews";
import FILTER_CONSTANTS from '../../utils/constants';
import {PATH_API_SEARCH} from '../../redux/Constants';
