import React, { Component } from 'react';
import "./Contact.css";
import axios from 'axios';
import $ from 'jquery';
import MetaTags from 'react-meta-tags';
/*import Header from "../../components/Header/Header";*/
import MapContact from "../../components/MapContact/MapContact";
//import PropertyMap from "../../components/PropertyMap/PropertyMap";


class Contact extends Component {
    constructor(props) {
        super(props);

        this.state = {
            address: 'Miami,FL',
            type: null
        }
    }

    handleButton = this.handleButton.bind(this);

    render() {

        return(
            <div className="page" id="page-top">
                <MetaTags>
                  <title>Contact Us - Commercial Real Estate | ComPropertee</title>
                  <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                  <meta name="description" content="Contact us. Search commercial real estate listings for sale and lease. Find best deals in commercial property, office space, land and industrial space in ComPropertee."/>
                </MetaTags>
                <div className="wrapper">
                  <div id="header">
                  
                  </div>
                  
                  <div id="page-content">
                    <div id="map2" className="map">
                      <MapContact address="13170 SW 128 Ste, Miami, FL 33186, EE. UU." title="13170 sw 128 Ste Suite 105, Miami Florida 33186"/>
                    </div>
                    <div className="container">
                      <div className="row">
                        <div className="contact-form">
                          <h2>We would love to hear from you</h2>
                          <h4>Your opinion is valuable</h4>
                          <h6>Monday to Sunday 8:00am to 8:00pm</h6>
                          <form id="form-submit" className="form-submit padtop20" action="">
                            <div className="col-md-6">
                              <div className="input-group">
                                <label>Your Name:</label>
                                <input id="input-name" type="text" name="name" className="form-control" placeholder="Enter your Name" required/>
                              </div>
                            </div>
                            <div className="col-md-6">
                              <div className="input-group">
                                <label>Your Email Address:</label>
                                <input id="input-email" type="text" name="email" className="form-control" placeholder="Enter your Email" required/>
                              </div>
                            </div>
                            <div className="col-md-12">
                              <div className="input-group">
                                <label>Subject:</label>
                                <input id="input-subject" type="text" name="subject" className="form-control" placeholder="Example: Lorem Ipsum" required/>
                              </div>
                            </div>
                            <div className="col-md-12">
                              <div className="input-group">
                                <label>Your Message:</label>
                                <textarea id="text-area-contact" rows="8" name="message" cols="45" className="form-control"></textarea>
                              </div>
                            </div>
                            <div className="submit">
                              <span className="ffs-bs"><button type="button" className="btn btn-large btn-primary" style={{"color":"#fff"}} onClick={this.handleButton}>Send Message</button></span>
                            </div>
                            <div className="col-md-12 padtop15 resmessage">
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                  
                  <div id="footer">

                  </div>
                </div>
            </div>
        );
    }

    handleButton(e){
      var forminfo = {name : $('#input-name').val(), subject : $('#input-subject').val(), email : $('#input-email').val(),message : $('#text-area-contact').val()}
            let API_URL = 'https://gooneworld.com/func/contact-us.php';
            axios.get(API_URL, {params:forminfo})
            .then((res) => {
                if(res.status === 200) {
                    $(".resmessage").html(res.data.render);
                    console.log(res.data);
                }
            })
            .catch((err) => {
               console.log(err);
            });

    }
}

export default Contact;