import React, {Component} from "react";
import {GoogleMap, withGoogleMap,Marker,InfoWindow} from "react-google-maps";
 
import {Constants} from '../../utils';
/* Load Services */
 import {find_lat_lng} from '../../services';

const google = window.google;
/*
 * Add <script src="https://maps.googleapis.com/maps/api/js"></script> to your HTML to provide google.maps reference
 */

/*
 * https://developers.google.com/maps/documentation/javascript/examples/drawing-tools
 *
 * Note: requires the Google Maps drawing API library in your script src
 *
 * Credits: thanks @idolize for the contribution!
 */

const iconUrl = require("../../assets/img/placeholder.png")

let iconMarker = null;
if(typeof window.google !== "undefined")
  iconMarker =  new window.google.maps.MarkerImage(
    /* "https://lh3.googleusercontent.com/bECXZ2YW3j0yIEBVo92ECVqlnlbX9ldYNGrCe0Kr4VGPq-vJ9Xncwvl16uvosukVXPfV=w300" */
    iconUrl,
    null, /* size is determined at runtime */
    null, /* origin is 0,0 */
    null, /* anchor is bottom center of the scaled image */
    new window.google.maps.Size(32, 32)
);

class MarkerWithInfoWindow extends React.Component {

  infoWindowStyle = {
    datarow:{
      fontSize: '11px',
      padding: '1px 0px 1px 0px',
      lineHeight: '1.0'
    }
  };
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      styleOfProperty : '',
      squareFootage: ''
    };
    this.onToggleOpen = this.onToggleOpen.bind(this);
    this.onMouseOver = this.onMouseOver.bind(this);
    this.onMouseOut = this.onMouseOut.bind(this);
  }

  onToggleOpen() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  onMouseOver = ()=> {
    //setAnimation
    this.setState({
      isOpen: true
    });
  };

  onMouseOut = ()=>{
    this.setState({
      isOpen: false
    });
  };

  priceToK = (price)=>{
    if(price && price>999){
      let _p = price/1000;
      return _p+'K';
    }else{
      return price;
    }
  };

  styleOfProperty = (data)=>{
    let styleOfProperty = '';
    let squareFootage = '';

    if(data.StyleofProperty!==''){
      styleOfProperty =  data.StyleofProperty
    }else if(data.PropertyType!==''){
      styleOfProperty = data.PropertyType;
    }else if(data.TypeofBuilding!==''){
      styleOfProperty = data.TypeofBuilding;
    }

    //finding Sqft
    if(data.ApproximateLotSize!=="" && data.ApproximateLotSize!=='0' && data.ApproximateLotSize!=='X' ){
      squareFootage = data.ApproximateLotSize +' sqft approx';
    }else if(data.SqFtTotal!=="" && data.SqFtTotal!=='0' && data.SqFtTotal!=='X' ){
      squareFootage = data.SqFtTotal + ' sqft total';
    }else if(data.SqFtOccupied!=="" && data.SqFtOccupied!=='0' && data.SqFtOccupied!=='X' ){
      squareFootage = data.SqFtOccupied + ' sqft occupied';
    }else if(data.Acreage!=="" && data.Acreage!=='0' && data.Acreage!=='X' ){
      squareFootage = data.Acreage + 'Acerage';
    }

    this.setState({
      styleOfProperty : styleOfProperty,
      squareFootage: squareFootage
    })
  };


  componentDidMount() {
    this.styleOfProperty(this.props.data.data);
  }

  render() {
    return (<Marker
        key={this.props.key}
        icon={iconMarker}
        /* animation= {google.maps.Animation.BOUNCE} */
        position={this.props.position}
        onMouseOver={this.onMouseOver}
        onMouseOut={this.onMouseOut}
        >
      {this.state.isOpen && <InfoWindow onMouseOver={this.onMouseOver} onMouseOut={this.onMouseOut} onCloseClick={this.onToggleOpen}
      style={{marging:"0px", padding:"0px"}} className="market-propertee">
        <div style={{height:'auto',width:'200px',display:"block"}}>
          <div className="cover-marker" style={{height:"150px",width:"100%",     
          background:`url(${Constants.URL_API_PHOTOS}${this.props.data.data.Matrix_Unique_ID}/0.jpg)`, backgroundSize: "cover",backgroundPosition: "center center"} }>
            
          </div>
          <div style={{height:"100px",width:"100%",display:"block",marginTop:"10px",padding:"10px"}}>
            <p style={{ fontSize: '14px',
              padding: '1px 0px 1px 0px',
              lineHeight: '0.5'
            }}><strong>${this.priceToK(this.props.data.data.ListPrice)}</strong></p>
            <p style={this.infoWindowStyle.datarow}>{this.state.styleOfProperty}</p>
            <p style={this.infoWindowStyle.datarow}>{this.state.squareFootage}</p>
          </div>
        </div>
      </InfoWindow>}
    </Marker>)
  }
}

const DrawingGoogleMap = withGoogleMap(props => (
  <GoogleMap
    defaultZoom={10}
    defaultCenter={new google.maps.LatLng(props.center.lat,props.center.lng )}
  >
    {props.markers}
  </GoogleMap>
));

let _markers = [];

export default class DrawingView extends Component {

  state = {
    markers : [],
    isOpen : true
  };

  createMarker = (key,coords,data) =>{
    return (<MarkerWithInfoWindow
        key={{key}}
        position={{lat:coords.lat,lng:coords.lng}}
        data={{data}}
    />)
  };

  getPosition = async (listingId)=> {

    //Find Lat and Lng
    find_lat_lng(listingId)
    .then((res)=>{
      //this.state.markers.push(<Marker position={{lat:res.data.latitude,lng:res.data.longitude}}/>)
      if(res.data && this.data){
        _markers.push(this.createMarker(listingId,{lat:res.data.latitude,lng:this.data.longitude}));
      }
    })
    .catch((err)=> console.log(err));
  };

  componentDidMount() {
    let listings = this.props.Listings;
    //let _markers = [];
    listings.map((l)=>{

      if(l.type !== "dummy"){
        if(l.hasOwnProperty('Geocode')){
          //_markers.push(<Marker onClick={this.onToggleOpen} key={l.id} position={{lat:l.Geocode.latitude,lng:l.Geocode.longitude}}/>)
          _markers.push(this.createMarker(l.id,{lat:l.Geocode.latitude,lng:l.Geocode.longitude},l));
        }else{
            this.getPosition(l.id);
        }
      }
      return l;
    });
    if(listings[0])
    if(listings[0].hasOwnProperty('Geocode')){
      this.setState({
        markers: _markers,
        center:{
          lat: listings[0].Geocode.latitude,
          lng: listings[0].Geocode.longitude
        }
      });
      
    }
  }

  componentWillReceiveProps(nextProps, nextContext) {
    if(this.props!==nextProps){
      //console.log('Drawing View',this.props.Listings);
      let listings = this.props.Listings;
      //let _markers = [];
      listings.map((l)=>{
        if(l.type !== "dummy"){
          if(l.hasOwnProperty('Geocode')){
            //_markers.push(<Marker onClick={this.onToggleOpen} key={l.id} position={{lat:l.Geocode.latitude,lng:l.Geocode.longitude}}/>)
            _markers.push(this.createMarker(l.id,{lat:l.Geocode.latitude,lng:l.Geocode.longitude},l));
          }else{
            this.getPosition(l.id);
          }
        }
        return l;
      });
      if(listings[0])
      if(listings[0].hasOwnProperty('Geocode')){
        this.setState({
          markers: _markers,
          center:{
            lat: listings[0].Geocode.latitude,
            lng: listings[0].Geocode.longitude
          }
        });
      }

    }
  }

  render() {
    if(this.state.markers.length<=0){
      return 'Loading...';
    }
    return (
      <DrawingGoogleMap
        loadingElement={<div style={{height: `100%`}}/>}
        containerElement={<div style={{height: `100vh`}}/>}
        mapElement={<div style={{height: `100%`}}/>}
        markers={this.state.markers.map((marker,index) => {
          return React.cloneElement(marker, {//Se elimin warning
                key:index
          })
        })}
        center={this.state.center}
      />
    );
  }
}
