import React,{Component} from 'react';
import {AdvancedForm} from '../../index';
import {Input,Icon, Button,Row} from 'antd';
import {Link} from "react-router-dom";

import './registerform.css'
class RegisterForm extends Component{
    render(){
        return(
            <AdvancedForm onSubmit={this.props.onSubmit} className="form-login" footer={
                <div>
                    <Row>
                        <Button size="large"  type="primary" htmlType="submit" className="login-form-button">
                            JOIN
                        </Button>
                        Do u have an account?  <Link to="/dashboard/login">Sign In</Link>
                    </Row>
                </div>
            }>
                <div> 
                    <Input size="large"  style={{width:'100%'}} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="User Name" name="username" validator={[{ required: true, message: 'Please input your user name!' }]}/>
                     <Input size="large"  style={{width:'100%'}} prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Email" name="email" validator={[{ required: true, message: 'Please input your email!' }]}/>
                     <Input size="large" name="password"  style={{width:'100%'}} prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Password" type="password" validator={[{ required: true, message: 'Please input your password!' }]}/>
                    <Input type="password"  size="large" name="rp_password"  style={{width:'100%'}} prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Confirm Password" validator={[{ required: true, message: 'Please input your password!' }]}/>
                  
                </div>
            </AdvancedForm>
        );
    }
}
export default RegisterForm;