import  React, { Component } from 'react'
import {
    List, Skeleton, Pagination
} from 'antd';
import './infinitelist.css'

const TplSkeleton = (key) => {
    return (<div className="col-md-4 col-sm-6 col-xs-6 prop" key={key}>
        <div className="wht-cont">
          <Skeleton className="property-skeleton" active loading avatar={{ shape: 'square' }} title={false} paragraph={{ rows: 3 }}/>
        </div>
    </div>)
}

class InfiniteList extends Component{
  state = {
    loading:false,
    preloading:true,
    hasMore:true,
    data:[],
    pageStart:0,
    current:1,
    loadMore:true
  }
  displaySkeleton = () => {
    let count = 6;
    return Array.apply(null, {length: count})
    
    .map((item,index) => {
      return {
        id:index,
        type:"dummy"
      };
    })
  }

  componentDidMount() {
    const data = this.displaySkeleton();
    this.setState({
      data
    });
  }
  renderItem = (item) =>{
    if(typeof item !== "undefined"){
      if(this.props.preloading) return <TplSkeleton {...item}/>;
      return this.props.renderItem(item);
    }
    return;
  }
  loadMore = async () => {
   
    this.props.loadMore()
    .then(data =>{
        console.log("Data Loaded!",data);
    })
    .catch(()=>{

    });
  }
  onShowSizeChange = (current, pageSize) => {
    console.log(current, pageSize);
  }
  onChange = (page) => {
    console.log(page);
    this.setState({
      current: page,
    });
    if(typeof this.props.onChange !== "undefined"){
      this.props.onChange(page);
    }
  }
  render(){
      let {loading,preloading,pagination} = this.props;
      let {CurrentPage,TotalRecords} = pagination;
      let data = this.props.data;

      /* if(data.length == 0)
        data = this.displaySkeleton(); */
      let pageStart = this.props.pageStart || this.state-pageStart;
      //let preloading = (data.length === 0);

      if(loading || preloading){
        data = this.displaySkeleton();
      }

      return(
        <div style={{marginBottom:30}}>
          <div className={"demo-infinite-container view-"+(this.props.className || '')} id="scroll-container">

          <div className="scrolllist-container">
              <div className="scroll-list" id="scroll-list">
              {/* <InfiniteScroll

              initialLoad={false}
              pageStart={pageStart}
              loadMore={this.loadMore}
              hasMore={false}
              useWindow={false}
              >
              ...
              </InfiniteScroll> */}
                    <List
                      dataSource={data}
                      itemLayout="vertical"
                      
                      renderItem={(item,index) => (
                            
                          <div className="item-list">
                              {
                                this.renderItem(item)
                              }
                          </div>
                      )}>
                        
                    </List>
                    <div style={{position:"absolute",bottom:'0px',textAlign:"center",width:"100%"}}>
                        {
                          TotalRecords > 0 &&
                          <Pagination showTotal={total => `Total ${total} items`} size="small" onChange={this.onChange} current={this.state.current}  defaultCurrent={CurrentPage} total={TotalRecords} pageSize={30} />
                        }
                    </div>
              </div>
          </div>
           
            
          </div>
        </div>                    
      );
   }
  }
  export default InfiniteList;