import React, {Component} from 'react';
import {Input,Icon, Button,Row,message} from 'antd';
import './profileform.css';
import {AdvancedForm} from '../index';

import {get_user,update_user} from '../../../services';

class ProfileForm extends Component{
    constructor(props){
        super(props);
        this.state = {
            userId:localStorage.userId,
            username:null,
            email:null,
            firstname:null,
            lastname:null,
            phone:null,
            address:null,
            city:null,
            password:null,
            state:null
        }
    }

    onSubmit = (err,params) => {
        if(!err){
            console.log(err,params);
            update_user(params)
            .then(response => {
                let {data} = response;
                if(typeof this.props.onSubmit)
                    this.props.onSubmit(data);
                message.info(data.description || 'User updated!');
            })
            .catch(err => console.log(err));
        }
    }
    componentWillMount(){
        let {userId} = this.state;
        get_user(userId)
        .then(response => {
            let {data} = response;
            this.setState({
               ...data
            });
        })
        .catch(err => console.log(err));
    }
    render(){
        let {
            username,
            email,
            firstname,
            lastname,
            phone,
            address,
            city,
            password,
            state
        } = this.state;

        
        return(
            <AdvancedForm formLayout="vertical"  onSubmit={this.onSubmit}  footer={
                <div>
                    <Row>
                        <Button size="large" style={{width:"100%"}} type="primary" htmlType="submit" className="login-form-button">
                            UPDATE
                        </Button>
                    </Row>
                </div>
            }>
                <div> 
                    <Input size="large"  style={{width:'50%'}}   
                    placeholder="First Name" initial={firstname} name="firstname" validator={[{ required: false, message: 'Please input your First name!' }]}/>

                    <Input size="large"  style={{width:'50%'}}   
                    placeholder="Last Name" initial={lastname} name="lastname" validator={[{ required: false, message: 'Please input your last name!' }]}/>

                    <Input size="large"  style={{width:'100%'}}  
                    placeholder="Phone" initial={phone} name="phone" validator={[{ required: false, message: 'Please input your phone!' }]}/>


                    <Input size="large"  style={{width:'50%'}}  
                    placeholder="City" initial={city} name="city" validator={[{ required: false, message: 'Please input your city!' }]}/> 

                    <Input size="large"  style={{width:'50%'}}  
                    placeholder="State" initial={state} name="state" validator={[{ required: false, message: 'Please input your state!' }]}/> 
                    
                    <Input size="large"  style={{width:'100%'}}  
                    placeholder="Address" initial={address} name="address" validator={[{ required: false, message: 'Please input your adress!' }]}/> 

                    <Input size="large"  style={{width:'50%'}}   
                    placeholder="User Name" initial={username} name="username" validator={[{ required: true, message: 'Please input your password!' }]}/>
                     <Input size="large"  style={{width:'50%'}} 
                    placeholder="Email" initial={email} name="email" validator={[{ required: true, message: 'Please input your password!' }]}/>

                    <Input size="large"  style={{width:'100%'}} prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Current Password" type="password" initial={password} name="password" validator={[{ required: true, message: 'Please input your current password!' }]}/>

                </div>
            </AdvancedForm>
        );
    }
}
export default ProfileForm;