import React, { Component } from "react";
import { Menu, Icon, message, Modal, Tabs } from "antd";
import { withRouter } from "react-router";
import "./sidemenu.css";

import { MyModal, ChangePasswordForm, ProfileForm } from "../index";
import { logout } from "../../../services";

const TabPane = Tabs.TabPane;
const confirm = Modal.confirm;

class SideMenu extends Component {
  state = {
    current: "home",
    open: false
  };
  showConfirm = () => {
    let { history } = this.props;
    confirm({
      centered: true,
      title: "Atention!",
      content: "Do you want to close the session?",
      onOk() {
        return logout({
          access_token: localStorage.access_token
        })
          .then(response => {
            localStorage.clear();
            history.push("/dashboard/login");
          })
          .catch(err => {
            message.info(err.message);
            localStorage.clear();
            history.push("/dashboard/login");
          });
      },
      onCancel() {}
    });
  };
  handleClick = option => {
    let { key } = option;
    let { history } = this.props;
    this.setState({
      current: key
    });
    switch (key) {
      case "login":
        return history.push("/dashboard/login");
        break;
      case "signup":
        return history.push("/dashboard/register");
        break;
      case "for_sale":
        return history.push("/listings/search/Miami/FL/for_sale");
        break;
      case "for_lease":
        return history.push("/listings/search/Miami/FL/for_lease");
        break;

      case "logout":
        this.showConfirm();
        break;
      case "profile":
        this.setState({
          open: true
        });
        break;
      default:
        return history.push("/");
        break;
    }
    if(typeof this.props.onClick !== "undefined") this.props.onClick(option);
  };

  render() {
    let { open } = this.state;
    return (
      <div>
        <Menu
          onClick={this.handleClick}
          selectedKeys={[this.state.current]}
          mode="inline"
          theme="dark"
        >
          <Menu.Item key="home">
            <Icon type="home" />
            Home
          </Menu.Item>
          <Menu.Item key="for_sale">
            <Icon type="appstore" />
            For Sale
          </Menu.Item>
          <Menu.Item key="for_lease">
            <Icon type="appstore" />
            For Lease
          </Menu.Item>
          {!localStorage.userId && (
            <Menu.Item key="signup">
              <Icon type="appstore" />
              Sign up
            </Menu.Item>
          )}
          {!localStorage.userId && (
            <Menu.Item key="login">
              <Icon type="appstore" />
              Sign In
            </Menu.Item>
          )}
          {localStorage.userId && (
            <Menu.Item key="profile">
              <Icon type="user" />
              Profile
            </Menu.Item>
          )}
          {localStorage.userId && (
            <Menu.Item key="logout">
              <Icon type="appstore" />
              Log out
            </Menu.Item>
          )}
        </Menu>
        <MyModal
          title={
            <span>
              <Icon type="user" /> USER PROFILE
            </span>
          }
          visible={open}
          width={400}
          onCancel={open => this.setState({ open })}
        >
          <Tabs type="card">
            <TabPane tab="User Data" key="1">
              <ProfileForm onSubmit={() => this.setState({ open: false })} />
            </TabPane>
            <TabPane tab="Security" key="2">
              <ChangePasswordForm
                onSubmit={() => this.setState({ open: false })}
              />
            </TabPane>
          </Tabs>
        </MyModal>
      </div>
    );
  }
}
export default withRouter(SideMenu);
