import  React from 'react'
import { Select } from 'antd';
import querystring from 'querystring';
import debounce from 'lodash/debounce';
import {Api} from '../../../utils';

import './searchfield.css';
const Option = Select.Option;


class SearchField extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      data: [],
      loading:false,
      value: 'Miami',
    }
    this.fetch = debounce(this.fetch,300);
  }

  handleSearch = (value) => {
    console.log("Value: ",value);
    this.fetch(value, data => this.setState({ data }));
  }

  handleChange = (value) => {
    if(this.props.onChange !== "undefined")
      this.props.onChange(value);
    this.setState({ value });
  }
  componentDidMount(){
    /* this.state.value = this.props.value || this.state.value; */
  }
  fetch = (value, callback) => {
    const self = this;
    let {url} = this.props;
    let root = this.props.root || 'search';
    

    this.setState({
      loading:true,
      data:[]
    });

    return Api.get(url+'?'+querystring.encode({"query":value, code: 'utf-8'}))
      .then(rs => {

        self.setState({loading:false});

        var response = rs.data || {};
        if(typeof response[root] !== "undefined"){

          var data = response[root] || [];
          data = data.map(function(item){
            return{
              value: item,
              text:item
            };
          });
          callback(data);
        }
      })
      .catch(err => console.log(err));
  
   
  }
  render() {

    let data = this.state.data || [];
    const options = data.map(d => <Option key={d.value}>{d.text}</Option>);
    return (
      <Select
        showSearch={this.props.showSearch || true}
        defaultValue={this.props.defaultValue || null}
        value={this.state.value}
        className={this.props.className || "searchfield"}
        loading={this.state.loading || false}
        suffixIcon={this.props.suffixIcon || null}
        placeholder={this.props.placeholder || ''}
        style={this.props.style}
        size={this.props.size}
        defaultActiveFirstOption={false}
        showArrow={this.props.showArrow || false}
        filterOption={false}
        onSearch={this.handleSearch}
        onChange={this.handleChange}
        notFoundContent={null}
      >
        {options}
      </Select>
    );
  }
}

export default SearchField;