import React from 'react';
import {Map, InfoWindow, Marker,GoogleApiWrapper} from 'google-maps-react';
import Geocode from "react-geocode";

const API_KEY = "AIzaSyBIIMCtAQw9VFVhzdRxDe9XROOGAqqY0ps";
export class PropertyMap extends React.Component {

  state = {
    showingInfoWindow: false,  //Hides or the shows the infoWindow
    activeMarker: {},          //Shows the active marker upon click
    selectedPlace: {},          //Shows the infoWindow to the selected place upon a marker
    Address: "",
    MarkerPosition : {lat:'',lng:''},
    Title : '',
    SubTitle: '',
    MapStyle : {
      width: '100%',
      height: '350px'
    }
  };

  initStreetView({map, maps}) {
    var sv = new maps.StreetViewService();
    var panorama = new maps.StreetViewPanorama(document.getElementById('map'));
    sv.getPanorama({location: {lat: 37.869, lng: -122.2547}, radius: 50}, processSVData);

    function processSVData(data, status) {
     /*  var marker = new maps.Marker({
        position: data.location.latLng,
        map: map,
        title: data.location.description
      }); */
      panorama.setPano(data.location.pano)
      panorama.setPov({
        heading: 270,
        pitch: 0
      });
      panorama.setVisible(true);
    }
  }

  componentDidUpdate(){
    if(this.state.Address!==this.props.address){
      
      this.setState({
        Address: this.props.address,
        Title: this.props.title,
        SubTitle: this.props.subTitle
      });

      Geocode.setApiKey(API_KEY);
      Geocode.enableDebug();
      Geocode.fromAddress(this.props.address).then(
        response => {
          const { lat, lng } = response.results[0].geometry.location;
          this.setState({
            center:{
              lat:lat,
              lng:lng
            },
            MarkerPosition:{
              lat:lat,
              lng:lng
            },
            Address: this.props.address,
            Title: this.props.title,
            SubTitle: this.props.subTitle
          });
        },
        error => {
          console.error(error);
        }
        );
    }
  }

  apiIsLoaded = (map, maps) => {
    if (map) {
      let panorama = maps.StreetViewPanorama(
        this.refs.panorama, {
          position: { lat: -34.397, lng: 150.644 },
          pov: {
            heading: 34,
            pitch: 10,
          },
        });

      map.setStreetView(panorama);
    }
  };

  render(){
    return(
      <Map 
      google={this.props.google} 
      zoom={16}
      center={this.state.MarkerPosition}
      style = {this.state.MapStyle}
      onGoogleApiLoaded={({ map, maps }) => this.apiIsLoaded(map, maps)}
      >
      <Marker
      id={1}
      position={this.state.MarkerPosition}
      onClick={this.onMarkerClick}
      title={this.state.Title}
      subtitle={this.state.SubTitle}
      />
      <InfoWindow
      marker={this.state.activeMarker}
      visible={this.state.showingInfoWindow}
      onClose={this.onClose}
      >
      <div>
      <h4>{this.state.selectedPlace.title}</h4>
      <h6>{this.state.selectedPlace.subtitle}</h6>
      </div>
      </InfoWindow>
      </Map>
      );
    }

    onMarkerClick = (props, marker, e) =>
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    onClose = props => {
      if (this.state.showingInfoWindow) {
        this.setState({
          showingInfoWindow: false,
          activeMarker: null
        });
      }
    };

  }

  export default GoogleApiWrapper({
    apiKey: API_KEY
  })(PropertyMap)

