import React, {Component} from "react";
import {GoogleMap, OverlayView, StreetViewPanorama, withGoogleMap} from "react-google-maps";

//const coordinates = {lat: 49.2853171, lng: -123.1119202};

const STYLES = {
  overlayView: {
    background: `red`,
    color: `white`,
    padding: 5,
    borderRadius: `50%`,
  },
};

function getPixelPositionOffset(width, height) {
  return {x: -(width / 2), y: -(height / 2)};
}

const StreetViewPanoramaExampleGoogleMap = withGoogleMap(props => (
    <GoogleMap
        defaultZoom={8}
        defaultCenter={props.coords}
    >
      <StreetViewPanorama
          defaultPosition={props.coords}
          defaultPov={{heading:270}}
          visible
      >
        <OverlayView
            position={props.coords}
            mapPaneName={OverlayView.OVERLAY_LAYER}
            getPixelPositionOffset={getPixelPositionOffset}
        >
          <div style={STYLES.overlayView}>
            OverlayView
          </div>
        </OverlayView>
      </StreetViewPanorama>
    </GoogleMap>
));

/**
 * You can pass in an `containerElement` to render `StreetViewPanorama` in its own containers
 * At this point the `GoogleMap` wrapper and `withGoogleMap` HOC become optional,
 * so you can either render a map and StreetView at the same time,
 * or just the StreetView on its own
 *    <StreetViewPanorama
 *      containerElement={<div styles={{ width: `100%`, height: `100%` }} />}
 *      defaultPosition={coordinates}
 *      visible
 *    />
 */

/*
 * Add <script src="https://maps.googleapis.com/maps/api/js"></script> to your HTML to provide google.maps reference
 */
export default class StreetViewPanoramaExample extends Component {

  render() {
    return (
        <StreetViewPanoramaExampleGoogleMap
            loadingElement={<div style={{height: `100%`}}/>}
            containerElement={<div style={{height: `350px`}}/>}
            mapElement={<div style={{height: `100%`}}/>}
            coords={this.props.coords}
        />
    );
  }
}
