import  React, { Component } from 'react'
import { Form } from 'semantic-ui-react'
const MAX = 50;
class ComboBox extends Component{
	constructor(){
	  	super();
	  	this.state = {
	  		error:"",
	  		options:[],
	  		filter:"",
	  		value:"",
	  		loaded:false
	  	}
	  	this.handleChange = this.handleChange.bind(this); 
  	}
  	handleChange(e,{name,value}){
          console.log(value);
  		this.setState({
  			"value":value
  		});
  		if(this.props.onChange){
  			this.props.onChange(e,{name,value});
  		}
  	}
  	renderResultados = (value) => {
  		this.setState({
  			"options":(typeof(this.props.filterOptions)!="undefined")?this.props.filterOptions:[]
  			.filter(item => {
				return (item.text.toLowerCase().match(`.*${value.toLowerCase()}.*`));
			})
		});
  	}
  	/*
  	** Funcion Debounce: 
  	** Mejora la Experiencia de usuario. ya que se ejecuta solo una vez al terminar de escribir.
  	*/
  	debounce = (fn,tiempo) => {
  		let timeoutId;
  		return function(){
  			if(timeoutId){
  				clearTimeout(timeoutId);
  			}
  			const context = this;
  			const args = arguments;
  			timeoutId = setTimeout(() => {
  			  fn.apply(context,args);
  			}, tiempo);
  		}
  	}
  	componentWillReceiveProps(nextProps,context) {
  		let {value, filterOptions} = nextProps;
  		if(filterOptions){
			if(filterOptions.length>0){
				if(value!==""){
			  		this.setState({
			  			value,
						"options":filterOptions.filter(item => {
							return (item.value === value);
						}).slice(0,MAX)
					});	
				}else{
					this.setState({
						"options":(typeof(filterOptions)!=="undefined")?filterOptions.slice(0,MAX):[]
					});
				}
			}
  		}
  	}
  	componentWillMount(){		
  		var me = this;
  		if(this.props.value!=""){
	  		this.setState({
	  			"value":this.props.value,
				"options":(typeof(this.props.filterOptions)!="undefined")?this.props.filterOptions.slice(0,MAX):[].filter(item => {
					return (item.value === me.props.value);
				})
			});
	  	}else{
	  		this.setState({
	  			"options":(typeof(this.props.filterOptions)!="undefined")?this.props.filterOptions.slice(0,MAX):[]
	  		});
	  	}
  	}
  	render(){

  		const renderFilterDebounced = this.debounce(this.renderResultados,1000);
  		return(
  			<Form.Field> 
                  <Form.Dropdown 
                   className={this.props.className || ''}
	  				required={(typeof(this.props.validator)!="undefined")?this.props.validator.required:false}
	  				label={this.props.label}
	  				name={this.props.name}
	  				noResultsMessage={(typeof(this.props.noResultsMessage)!="undefined")?this.props.noResultsMessage:(typeof(this.props.placeholder)!="undefined")?this.props.placeholder:"Digite parmetros de busqueda."}
	  				icon={this.props.icon || 'search'}
	  				selection
	  				search
	  				value={this.props.value} 
	  				placeholder={this.props.placeholder}
	  				validator={this.props.validator}
	  				options={this.state.options}
	  				onChange={this.handleChange} 
	  				onKeyUp={(e) => {
	  					e.preventDefault();
	  					if(e.key !== 'Enter'){
	  						renderFilterDebounced(e.target.value);
	  					}
	  				}}
	  			/>
  			</Form.Field>
		);
  	}
}

export default ComboBox;
