import React,{Component} from 'react';
import {AdvancedForm} from '../../index';
import {Input,Icon, Button,Checkbox,Row} from 'antd';
import {Link} from "react-router-dom";

import './loginform.css'
class LoginForm extends Component{
    onChange = value => {
        //console.log("Value: ",value);
    }
    
    render(){
        return(
            <AdvancedForm 
            onSubmit={this.props.onSubmit}    
            className="form-login" footer={
                <div>
                    <Row>
                        <Checkbox>Remember me</Checkbox>
                        <a href="/dashboard/password-reset" className="login-form-forgot">Forgot password</a>
                    </Row>
                    <Row>
                        <Button size="large"  type="primary" htmlType="submit" className="login-form-button">
                            Log in
                        </Button>
                        Or <Link to="/dashboard/register">register now!</Link>
                    </Row>
                </div>
            }>
                <div> 
                    <Input onChange={this.onChange}
                    validator={[{ required: true, message: 'Please input your username!' }]}
                    size="large"  style={{width:'100%'}} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Username" name="email" />
                    <Input onChange={this.onChange} 
                    validator={[{ required: true, message: 'Please input your password!' }]}
                    size="large"  style={{width:'100%'}}  
                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    type="password" 
                    placeholder="Password" name="password" />
                </div>
            </AdvancedForm>
        );
    }
}
export default LoginForm;