import React, {Component} from 'react';
import { withRouter } from 'react-router'
import {Input,Icon, Button,Row,message} from 'antd';
import './changepassword.css';
import {AdvancedForm} from '../../index';

import {change_password} from '../../../../services';

class ChangePassword extends Component{
    constructor(props){
        super(props);
        this.state = {
            oldPassword:null,
            newPassword:null
        }
    }

    onSubmit = (err,params) => {
        let {history} = this.props;
        if(!err){
            change_password(params)
            .then(response => {
                let {data} = response;
                if(typeof this.props.onSubmit)
                    this.props.onSubmit(data);
                    localStorage.clear();
                    history.push("/dashboard/login");
                    message.info(data.description || 'Password changed!');
            })
            .catch(err => console.log(err));
        }
    }
    render(){
        return(
            <AdvancedForm formLayout="vertical"  onSubmit={this.onSubmit}  footer={
                <div>
                    <Row>
                        <Button size="large" style={{width:"100%"}} type="primary" htmlType="submit" className="login-form-button">
                            CHANGE PASSWORD
                        </Button>
                    </Row>
                </div>
            }>
                <div> 

                    <Input size="large"  style={{width:'100%'}} prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="Current Password"  type="password" name="oldPassword" validator={[{ required: true, message: 'Please input your current password!' }]}/>

                    <Input size="large"  style={{width:'100%'}} prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                    placeholder="New Password" type="password" name="newPassword" validator={[{ required: true, message: 'Please input your new password!' }]}/>

                </div>
            </AdvancedForm>
        );
    }
}
export default withRouter(ChangePassword);