import React,{Component} from 'react';
import {RegisterForm} from '../../../components/com';
import {signup} from '../../../services'
import { message} from 'antd';

import './register.css';


class Register extends Component{
    onSubmit = (err,params) => {
        let {history} = this.props;
        if(!err){
            if(params["password"] !== params["rp_password"]) return message.error("Passwords do not match!");
            delete params["rp_password"];
            signup(params)
            .then(response => {
                let {data} = response;
                console.log("Response: ",data);
                history.push("/dashboard/login");
            })
            .catch(err => message.error(err.message));
        }
    }
    render(){
        return(
            <section id="wrapper" className="login-register">
                <div className="login-box login-sidebar">
                    <div className="white-box">
                        <div className="conteim">
                            <a href="/" class="db titlogo">CP</a>
                        </div>
                        <RegisterForm  formLayout="vertical" onSubmit={this.onSubmit}/>
                    </div>
                </div>
            </section>
        );
    }
}
export default Register;