//CalculatorMortgage.js
import React,{Component} from 'react';
import "./calculatormortgage.css";

class CalculatorMortgage extends Component{

	constructor(props){
		super(props);
		this.state = {
			price_home:'',
			percent_down_payment:'',
			down_payment:'',
			years:'',
			rate_in:'',
			Address:''
		}

	}

	onChange = e => {
		let {value, name} = e.target;
		console.log("value y name", value, name);
		
		this.setState({
			[name] : value
		});
	}

	onDoCalculaDown = e =>{
		let payload = this.state;
		if(typeof this.props.onCalculaDown !== "undefined"){
			this.props.onCalculaDown(payload);
		}
	}

	componentDidMount(){
		let {
			Address,
			price_home,
			percent_down_payment,
			years,
			rate_in} = this.props;

		this.setState({
			Address,
			price_home,
			percent_down_payment,
			years,
			rate_in
		});

		this.onDoCalculaDown();
	}

	render(){
		let {
			Address,
			price_home,
			percent_down_payment,
			down_payment,
			years,
			rate_in
		} = this.state;

		return(
			<div>
				<div>
                    <h3 className="subti3"><span>Mortgage</span>
                        <span className="espec_hidd"> for {Address}</span>
                    </h3>
                </div>
                <div className="_row">
                    <div className="row col-md-12 palabreo_2" style={{'paddingBottom': 20 + 'px','fontSize':15 +'px'}}>Calculate your monthly mortgage payments for {Address}</div>
                    <div className="row col-md-6 col-xs-12">
                                <form name="frm_mortgage" id="frm_mortgage">
                                    
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12">
                                        <label>Home Price $</label>
                                        <input type="text" name="price_home" id="price_home"
                                               placeholder="Home Price" className="form-control"
                                               onChange={this.onChange}
                                               value={price_home}/>
                                        </div>
                                    </div>
                                    
                                    
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12">
                                        <label>Percentage Down Payment %</label>
                                        <input type="text" name="percent_down_payment"
                                               id="percent_down_payment"
                                               className="form-control input-sm text-left"
                                               /* value={percent_down_payment} */
                                               placeholder="Percentage" /* onCalculaDown={this.onDoCalculaDown} */
                                               onChange={this.onChange} defaultValue="20"
                                               maxLength="2"/>
                                        </div>
                                    </div>
                                    
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12">
                                        <label>Down Payment $</label>
                                        <input type="text" name="down_payment" id="down_payment"
                                               className="form-control" readOnly={true} maxLength="10"
                                               placeholder="Down Payment" onChange={this.onChange}
                                               value={down_payment}/>
                                        </div>
                                    </div>
                                    
                                    
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12">
                                        <label>Years</label>
                                        <input type="text" name="years" placeholder="Years"
                                               className="form-control" onChange={this.onChange}
                                               value={years} maxLength="2"/>
                                        </div>
                                    </div>
                                    
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12">
                                        <label>Interest Rate %</label>
                                        <input type="text" name="rate_in" placeholder="Rate Annual"
                                               className="form-control" onChange={this.onChange}
                                               value={rate_in}/>
                                        </div>
                                    </div>
                                    
                                    <div className="row">
                                        <div className="col-md-12 col-xs-12 dconcalcula">
                                        <input type="button" name="calcula_mort" id="calcula_mort"
                                               onClick={this.handleCalculaMort} value="Calculate"
                                               className="btn btn-small btn-primary"/>
                                        </div>
                                    </div>
                        </form>
                    </div>
                    <div className="col-md-6 col-xs-12">
                        <div className="circle_amount">
                            <div className="amount_payment">
                                <span>Your Monthly Payment</span>
                                <div className="amount_monthly"></div>
                            </div>
                        </div>
                    </div>
                    </div>

                </div>
		);
	}
}
export default CalculatorMortgage;
