import React, { Component } from 'react';
import { Radio } from 'antd';
import { Menu } from 'semantic-ui-react';

import './typeview.css';


class TypeViews extends Component {

    state = {
        value:'map'
    }
    handleChange = (e) =>{
        var el = e.target;
        this.setState({
            value:el.value
        });
        if(typeof this.props.handleView !== "undefined"){
            this.props.handleView(el.value);
        }
    }
    render() {
        let {value} = this.state;
        return  (
            <ul className="tab-links col-md-4 col-xs-12">
                <Menu.Item position="right" className="menu-type">
                    <Menu.Item>
                        <Radio.Group value={value} onChange={this.handleChange}>
                            <Radio.Button value="map"><i className="fa fa-map" aria-hidden="true"></i> Map</Radio.Button>
                            <Radio.Button value="grid"><i className="fa fa-table"></i>Grid</Radio.Button>
                            <Radio.Button value="list"><i className="fa fa-th-list"></i>List</Radio.Button>
                        </Radio.Group>
                    </Menu.Item>
                </Menu.Item>
			</ul>
        )
    }
}

export default TypeViews;